	module.exports = {"data":{"markdownRemark":{"html":"<h1>June 29, 2016</h1>\n<ul>\n<li>\n<p>Bart Spaans gave a demo and talked about <a href=\"https://github.com/opencredo/kubefuse\">KubeFuse</a></p>\n<ul>\n<li>Work with your Kubernetes cluster like it's a file system</li>\n<li>You can also edit resources and update them in cluster on writes</li>\n<li>Find more details in this <a href=\"https://opencredo.com/introducing-kubefuse-file-system-kubernetes/\">post</a></li>\n<li>Contact Bart on <a href=\"https://twitter.com/Work_of_Bart\">Twitter</a>: @Work<em>of</em>Bart</li>\n</ul>\n</li>\n<li>\n<p>The DRUD team showed off how they use Kubernetes Jobs</p>\n<ul>\n<li>DRUD is a drupal and wordpress management platform built on Kubernetes</li>\n<li>KubeJobWatcher can be found <a href=\"https://github.com/drud/KubeJobWatcher\">here</a></li>\n<li>For help or questions, reach out to Erin at erin@newmediadenver.com</li>\n</ul>\n</li>\n<li>Matt Farina is looking for people to help analyze the data from the sig-apps survey. Volunteers are welcome to reach out to him on <a href=\"http://twitter.com/mattfarina\">Twitter</a> or Slack: @mattfarina.</li>\n</ul>\n<p>Watch the <a href=\"https://youtu.be/6wA_YdgWL8o\">recording</a>.</p>"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-apps/minutes/2016-06-29/"}}

